body {
  background: #bfd6f5;
  margin: 10px;
  padding: 0px;
}

div {
padding: 10px;  
}

.flex-box {
  background-color: white;
  margin: 20px;
  width: 10%;
  border: 3px solid black;
  flex-basis: 25%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}


